.discount-link {
    position: absolute;
    font-size: 10px;
    margin: 1px;
    cursor: pointer;
}

.package-orderlist {
    -webkit-font-feature-settings: 'smcp', 'c2sc';
    font-feature-settings: 'smcp', 'c2sc';
    background-color: #f0f3f5;
    text-transform: uppercase;
    width: 70px;
    text-align: center;
    padding: 1px;
    border: 1px solid #e0e3e5;
    letter-spacing: 0.5px;
    padding: 1px 10px 2px;
    color: #333 !important
}

.kendo-input-suggestions-item,
.kendo-input-product-suggestions-items {
    display: -webkit-box;
    display: flex;
    border-bottom: 1px solid #ddd;
    padding: 0.33333rem 0;
    font-size: 0.8rem !important;
    line-height: 1.5 !important;
    padding: 0.33333rem 0.66667rem !important;
    color: #333 !important;
}

    .kendo-input-suggestions-item > div {
        width: 33.33%;
        padding: 0 0.33333rem;
    }

    .kendo-input-suggestions-item h3 {
        font-weight: 500;
    }

    .kendo-input-suggestions-item p {
        margin-bottom: 0;
    }

    .kendo-input-product-suggestions-items > h3, .kendo-input-product-suggestions-items p {
        width: 40%;
    }

    .kendo-input-product-suggestions-items > p:last-child {
        width: 20%;
    }

    .kendo-input-product-suggestions-items h3 {
        font-weight: 500;
    }

.kendo-input-product-suggestions-itemsp {
    margin-bottom: 0;
}

.block.no-border {
    border-bottom: 0 !important;
}

.block.no-padding-top {
    padding-top: 0;
}

.mo-prod-basket.no-margin {
    margin-top: 0;
}

.k-radio:checked + .k-radio-label::after {
    top: 3px;
    left: 3px;
}

.control-suffix.no-margin {
    margin-bottom: 0;
}

.mo-customer-table-wrapper {
    position: relative;
}

    .mo-customer-table-wrapper .jini-loader {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }

.product-subtitle {
    color: #333 !important;
}

.k-textbox.novaloadinggifsmall {
    background: url(Nova/loading.gif) no-repeat right center;
}

div.disabled {
    pointer-events: none;
    opacity: 0.5;
    background: #CCC;
}


.w100
{
    
    width: 100% !important;
}




table {
  width: 100%;
    
}
table th {
  text-align: left;
  border-bottom: 2px solid #ddd;
    
}
table th, table td {
  padding: .4em;
    
}

table.fold-table > tbody > tr.view td, table.fold-table > tbody > tr.view th {
  cursor: pointer;
}

/*table.fold-table td {

 &:nth-child(1) {
            width: 2%;
        }
}*/


table.fold-table .k-radio-label
{
    
    margin-top:  3px;
}




table.fold-table > tbody > tr.view td:last-child,
table.fold-table > tbody > tr.view th:last-child {
  position: relative;
  padding-left: 20px;
}
table.fold-table > tbody > tr.view td:last-child:before,
table.fold-table > tbody > tr.view th:last-child:before {
  position: absolute;
  top: 50%;
  left: 5px;
  width: 9px;
  height: 16px;
  margin-top: -8px;
  font: 16px fontawesome;
  color: #999;
  content: "\f078";
  transition: all .3s ease;
}
table.fold-table > tbody > tr.view:nth-child(4n-1) {
  background: #eee;
}
table.fold-table > tbody > tr.view:hover {
  background: #DEDEDE;
}
table.fold-table > tbody > tr.view.open {
  background: #dedede;
 
}
table.fold-table > tbody > tr.view.open td:last-child:before, table.fold-table > tbody > tr.view.open th:last-child:before {
  transform: rotateX(180deg);
  color: #333;
}
table.fold-table > tbody > tr.fold {
  display: none;
}
table.fold-table > tbody > tr.fold.open {
  display: table-row;
}

.fold-content {
  padding: .5em;
}
.fold-content h3 {
  margin-top: 0;
}
